
export const compareObjects = (a,b) => {
    const keys = Object.keys(a)
    
    if (keys.length===0) {
        return true
    }

	let keyIdx
	for (keyIdx in keys) {
		let ks = keys[keyIdx]
		if ( a[ks] !== b[ks] ) {
			return false
		}	
	}
	return true
}



export const unique = (t,c) => t.includes(c) ? t : t.concat(c)


export const isPossible = (possibleCombinations,attrname,value) => {
    return possibleCombinations.find(combo => combo[attrname]===value) ? true : false
    
}
