import React, { useState, useEffect } from 'react'

import Steps from './Steps'
import { updateToisio } from '../utils/toisio'
import Result from './Result'

import { Transition, Grid } from 'semantic-ui-react'

const ToisiolakiHelper = () => {

    
    useEffect(() => {
    
        const init = {"Tilaaja":"Kelan ulkopuolinen","Tiedon.taso":"yksilötaso","Pyynnon.kohde":"Etuustiedot, vain Kelan tietoja","Lakiperuste.kayttotarkoitus":"Tieteellinen tutkimus"}

        setToisio( updateToisio({}, 0) )



    },[])

    const [toisio, setToisio] = useState(null)
 
    if (toisio === null) {
        return('')
    }

    const addOption = (chosenOptions,chosenOption) => {
        chosenOptions[chosenOption.name] = chosenOption.value
        return chosenOptions
    }
    const onSelect = (option) => {
        
        const updateChoices = addOption(toisio.chosenOptions, option)
        setToisio( updateToisio( updateChoices, option.stepIndex + 1  ) )      
    }

    
    console.log('steps',toisio)

    return(
        <>
            
            <Grid columns={toisio.steps.length}>
                <Steps
                    steps={toisio.steps}
                    onSelect={onSelect}
                />                
            </Grid>
            <Grid columns={3}>
                <Grid.Column></Grid.Column>
                <Grid.Column stretched textAlign='center'>
                    <Result result={toisio.result}/>
                </Grid.Column>
                <Grid.Column></Grid.Column>            
            </Grid>
    
        </>
    )

}


export default ToisiolakiHelper