import React from 'react'
import { Icon, Grid, Segment, Header, Transition } from 'semantic-ui-react'


const Result = ({ result }) => {
    

    let lawBasis,permitType,permitGiver 
    let visible = false

    if (result)  {
        visible = true
        lawBasis     = result.lawBasis
        permitType   = result.permitType
        permitGiver  = result.permitGiver

    }
    

    const style = { color: '#21ba45' }

    
    return(
        <Transition
                    visible={ visible }
                    animation='scale'
                    duration={500}
                >

            
                <Segment>
                <Grid.Row verticalAlign='middle' >
                    <Icon name='law' size='huge'  style={style}/>
                </Grid.Row>
                <Grid.Row verticalAlign='middle'>
                    <Header as='h3'>
                        {  lawBasis }
                    </Header>
                </Grid.Row>
                {
                    permitGiver ? 
                        <><Grid.Row verticalAlign='middle'>
                            <Icon name='gavel' size='huge' style={style} />
                            </Grid.Row>
                            <Grid.Row verticalAlign='middle'>
                            <Header as='h3'>
                            {  permitGiver }
                            </Header>
                            </Grid.Row>
                            <Grid.Row verticalAlign='middle'>
                            <Header as='h4'>
                            {  permitType }
                            </Header>
                        </Grid.Row></>
                        :
                        ''
                    }
                </Segment>
            
    </Transition>
    )
    

}



export default Result