import { combinations } from './combinations'
import { steps as stepList } from './steps'


import { unique, compareObjects, isPossible } from './helpers'


// Creates 'toisio' object which
// Holds steps as array in which each step contains
//  -- options taken from combinations. 
//      -- And their attributes: isSelected, isPossible
//  -- Step attrs such as isCompleted, isActive
// Possible result object

const createToisio = (chosenOptions,activeStepIndex) => {

    const possibleCombinations = combinations.filter(combo => compareObjects(chosenOptions,combo) & combo['Luvan.myontaja.ja.lupa.']!=='Ei lakiperustetta' )  

    const steps = stepList.map(step => {
        return { 
            ...step
            ,isCompleted:   activeStepIndex > step.index
            ,isActive:      activeStepIndex === step.index
            ,options: combinations
                .map(combo => combo[step.attrname]) 				// Extract all values of step from combinations
                .reduce(unique,[])									// Distinct 
                .map(opt => (
                        // Turn each option into object
                        { 
                             value: opt
                            ,name: step.attrname
                            ,stepIndex: step.index
                            ,isChosen:   chosenOptions[step.attrname] === opt
                            ,isPossible: isPossible(possibleCombinations,step.attrname,opt)
                        }
                    ))  	
            }
    })
    // See if there's a chosen option for all steps
    // If so, take result
    const chosenSteps = steps.filter(step => step.options.find(option => option.isChosen)  ).reduce((t,c) => t.includes(c.index) ? t : t.concat(c.index),[])
    console.log('chosenSteps',chosenSteps)
    // Result

    let result  = undefined
    if (chosenSteps.length === steps.length) {
        console.log('possibleCombinations @ result',possibleCombinations)
        result  = createResult( possibleCombinations )
        
       

        
    }

    return {
        chosenOptions,
        activeStepIndex,
        steps,
        result
    }
}

const createResult = result => {

    
    if (result.length<1 ) {
        return { lawBasis: 'Ei lakiperustetta'} // Until all combos are listed
    } 


    let resultObject = result[0]

    let lawBasis = resultObject['Luvan.myontaja.ja.lupa.']
    let permitGiver = resultObject['Minne.tiedot']

    let permitType = ''
    if (permitGiver.match('/'))  {
        const splitted = permitGiver.split('/')
        
        console.log('splitted', splitted)
        permitType  = splitted[0].replace(/ {1,}$/,'')
        permitGiver = splitted[1].replace(/ {1,}$/,'')
    }

    return {
        resultObject,
        lawBasis,
        permitType,
        permitGiver

    }
}


export const updateToisio = (chosenOptions,activeStepIndex) => {
    return createToisio( chosenOptions === undefined ? {} : chosenOptions, activeStepIndex === undefined ? 0 : activeStepIndex)
}

