
import React from 'react'
import { Grid, Icon,  Button, ButtonContent, Header } from 'semantic-ui-react'

import  uuidv4  from 'uuid/v4'

const Steps = ({ steps, onSelect }) => {

    if (steps === null) {
        return ''
    }

    


    return (
        <>
            {
                steps.map(step => createStepButtons(step,onSelect))
            }

        </>
    )

}

const createStepButtons = (step,onSelect) => {

    const buttons = step.options.map(opt => { 

        const color =  opt.isChosen ? 'green' :  'blue'


        const style = {
            marginBottom: '2%',
            width: '100%',
            opacity: opt.isPossible | step.isCompleted ? '100%' : '75%'
            
        }

        return(
            
                <Button
                        key={uuidv4()}
                        size={'medium'}
                        color={color}
                        style={style}
                        onClick={() => onSelect(opt)}
                    >
                        <ButtonContent>
                            { opt.value }
                        </ButtonContent>
                    </Button>
            
                
        )
    })

    const stepStyle = {
        backgroundColor: step.isActive ? 'rgb(243, 240, 240)' : ''
    }

    return(
        <Grid.Column   key={uuidv4()} style={stepStyle} >
        {           
            <Header as='h4' icon={step.icon}>
        
                <Icon name={step.icon} />
                {step.title}
            </Header>
    
        }
        {
                buttons
        }
        </Grid.Column>
    )
    
    
}

export default Steps